.popup__title {
    max-width: 330px;
    font-family: 'Inter', 'Arial', sans-serif;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    margin: 0;
    margin-bottom: 50px;
    margin-top: 34px;
    align-self: flex-start;
    margin-left: 36px;
}

@media screen and (max-width: 320px) {
 .popup__title {
     max-width: 238px;
     font-size: 18px;
     line-height: 22px;
     margin: auto;
     margin-top: 25px;
     margin-bottom: 75px;
 }   
}